.form{
    margin-bottom: 25px;

}
.formButton{
    background-color: #E7DBCA;
          padding: 13px;
          color: rgb(38, 33, 33);
          width: 100px;
          border: 0;
          border-radius: 5px;
          font-size: 20px;
          font-weight: bold;
          cursor: pointer;
          transition: background-color 0.3s ease;
}
.formButton:hover {
    background-color: #999;
  }
.formInput{
    width: 200px;
  padding: 15px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f7f7f7;
  margin-right: 10px;
  outline: none;

}